import * as React from 'react';
import {styles} from './loader.module.scss';
import {useEffect, useState} from "react";


export const Loader = () => {
  const [progress , setProgress] = useState(0);
  const [finishAnimation , setFinished] = useState(0);
  const [error , setError]=useState(false);




  useEffect(()=>{
    const preloads =[ 'images/homebeauty.gif', 'images/homepage.gif' ,'/vmt/vmt.data' ,'vmt/vmt.wasm',  ];
    let i=  0 ;
    preloads.forEach((path )=>{
      setTimeout(()=>{
        setError(true)
      },20000)
      const f = document.createElement('link');
      f.href = path;
      f.rel='prefetch';
      document.body.append(f);
      f.addEventListener('load', ()=>{
        i++
        setProgress( Math.round(100/preloads.length * (i||1 ))   );
      } )     ;
      f.addEventListener('error', ()=>{
        setError(true);
        i++
        setProgress( Math.round(100/preloads.length * (i||1) )   );
      } )

    } )
  },[])

    const hide = {opacity:0 , pointerEvents:'none' };
    const finish = progress>98|| error;
  !finishAnimation&& console.log('loading progress : '+progress + '%')
    useEffect(()=>{
      if(finish){
        setTimeout(()=>{
          setFinished(1)
        },2000)      ;
      }
    },[finish])
  const spinner = [];
  for (let i = 0 ; i<101 ; i++){
    spinner.push(<i> <b> </b></i>)
  }

  return (
    <div style={finishAnimation  ?hide: {}} className={styles}>
      <div style={finishAnimation ?hide: {}} className="spinner">
        {spinner}
      </div>

    </div>
  );
};


